
.stretchy-wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
#ss-canvas {
  margin: 0 0 2em 0;
  width: 95%;
}
#ss-canvas #game-canvas {
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  border: 6px solid #e5e9f2;
  padding-bottom: 62.5%;
  border-radius: 10px;
  background-color: #fff;
}
#ss-canvas #word-guess {
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  background-color: #eff2f7;
  border: 6px solid #d3dce6;
  text-align: center;
  padding: 0 5%;
  margin: 0 auto;
  width: 6em;
  font-size: 2em;
  line-height: 1.25em;
  color: #5c6873;
  border-radius: 2em;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
#ss-canvas #word-guess:hover {
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
#ss-canvas #colour-palette {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 0;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 900px) {
#ss-canvas {
    margin: 2rem;
    width: 50%;
}
#ss-canvas #word-guess {
    top: -0.75em;
    font-size: 3em;
}
}


