
.colour {
  position: relative;
  top: 0.5rem;
  margin: 0 1px;
  transition: all 0.1s cubic-bezier(0.25, 0.8, 0.25, 1);
}
.colour button {
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
  position: relative;
  background-color: #85d5f7;
  width: 2.5rem;
  height: 2.5rem;
  border: 0.25rem solid #fff;
  border-radius: 50%;
}
.colour button:hover {
  width: 2.5rem;
  height: 2.5rem;
}
.colour button:focus {
  outline: 0;
}
@media (min-width: 768px) {
.colour {
    top: -2em;
}
.colour:active {
    padding-bottom: 1rem;
}
.colour button {
    width: 2.5rem;
    height: 2.5rem;
    border: 0.25rem solid #fff;
}
.colour button:hover {
    width: 3rem;
    height: 3rem;
}
}


