
#ss-players {
  color: #1f2d3d;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
  font-family: 'Lato', sans-serif;
}
#ss-players .status-box {
  text-align: center;
  padding: 0 0.5rem;
  margin: 0;
}
#ss-players .status-box h2 {
  margin: 0.5rem 0.25rem 0;
}
#ss-players .status-box h3 {
  margin: 0.5rem 0.25rem 0;
}
#ss-players .player-box {
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background-color: #f9fafc;
  margin: 0.5rem;
  border-radius: 6px;
  border: 2px solid #e5e9f2;
}
#ss-players .player-box ul {
  margin: 0;
  list-style: none;
  padding: 0.5rem;
  line-height: 1.25rem;
}
#ss-players .player-box ul li {
  padding: 0 0.5rem;
  margin: 0;
  border-radius: 6px;
  border: 1px solid #e5e9f2;
  position: relative;
}
#ss-players .player-box ul li div img {
  margin: 0 0.5rem 0 0;
  height: 50px;
  float: left;
  border-radius: 6px;
}
#ss-players .player-box ul li div h3 {
  padding: 0.5rem 0 0;
  margin: 0.5rem 0 0;
}
#ss-players .player-box ul li div p {
  padding: 0;
  margin: 0 0 0.5rem;
}
#ss-players .player-box ul li h4 {
  position: absolute;
  padding: 0.2rem 0.5rem;
  border-radius: 1px;
}
#ss-players .player-box ul li .you-banner {
  left: -0.75rem;
  top: -1.5rem;
