          transform: rotate(-7deg);
}
#ss-players .player-box ul li .drawing-banner {
  right: -0.75rem;
  top: -1.8rem;
  background-color: #f90;
  color: #f9fafc;
  -webkit-transform: rotate(7deg);
          transform: rotate(7deg);
}
#ss-players .player-box ul .highlighted {
  border-radius: 6px;
  border: 1px solid #e5e9f2;
}


